<template>
  <div class="age-pyramid-chart-container">
    <div
      class="chart-container"
    >
      <canvas
        ref="chartCanvas"
        :id="id"
      ></canvas>
    </div>
  </div>
</template>

<script>

import Chart from 'chart.js'
import chartConfig from '@/constants/charts/polarChart'

export default {
  name: 'PolarChart',
  props: {
    id: {
      type: String,
      required: true
    },
    surveyData:{
      type: Array,
      required: true
    }
  },
  computed: {
    chartDatasets () {
      const colors = ['rgba(255, 51, 92, 0.5)','rgba(18, 191, 206, 0.5)','rgba(198, 236, 174, 0.5)','#642b50','#8b9376','#b2916c']
      const data = [{
        data:[],
        backgroundColor:[]
      }]
      this.surveyData.forEach((l, i) => {
        data[0].data.push(parseInt(l.value))
        data[0].backgroundColor.push(colors[i])
      });
      return data
    },
    labels(){
      const labels = []
      for (let l of this.surveyData) {
        labels.push(`${l.label} ${l.value}`)
      }
      return labels
    }

  },
  methods:{
    buildChart(){
      if (this.chart != undefined) {
        this.chart.destroy()
      }
      const data = { datasets: this.chartDatasets, labels: this.labels }
      const doughnutChartConfig = { ...chartConfig, ...{ data } }
      const context = this.$refs.chartCanvas.getContext('2d')
      // context.height = 500;
      this.chart = new Chart(context, doughnutChartConfig)

      window.addEventListener('resize', () => {
        this.chart.destroy()
        this.chart = new Chart(context, doughnutChartConfig)
      })
    }
  },

  mounted () {
    this.buildChart()
  },
  updated(){
    this.buildChart()
  }
}

</script>

<style scoped>
canvas {
  margin: auto;
}
.age-pyramid-chart-container {
  margin: 12px 5px 16px 0;
  border: 1px solid #b3b3b3;
  height: 300px
}
.chart-container{
  height: 100%;
}
</style>
