<template lang="html">
  <div class="d-flex flex-column">
    <div class="d-flex flex-row align-center">
      <v-checkbox
          v-model="showInformalCamps"
          class="checkbox"
          hide-details
        ></v-checkbox>
        <img :src="imgURL+'spontaneous_site.png'" alt="" class="legend-marker mr-2">
        <span>Informal Settlements</span>
    </div>
    <div class="d-flex flex-row align-center">
      <v-checkbox
          v-model="showFormalCamps"
          class="checkbox"
          hide-details
        ></v-checkbox>
        <img :src="imgURL+'default.png'" alt="" class="legend-marker mr-2">
        <span>Planned Camps</span>
    </div>
  </div>
</template>

<script>
const devEnv = process.env.NODE_ENV === 'development'
const imgURL = devEnv ? '/markers/' : `./markers/`

export default {
  name: 'MarkersFilters',
  data(){
    return {
      imgURL: imgURL
    }
  },
  computed:{
    showFormalCamps:{
      get(){
        return this.$store.state.showFormalCamps
      },
      set(v){
        this.$store.commit("togglFormalCamps", v)
        this.$store.commit("filterCamps")
      }
    },
    showInformalCamps:{
      get(){
        return this.$store.state.showInformalCamps
      },
      set(v){
        this.$store.commit("togglInformalCamps", v)
        this.$store.commit("filterCamps")
      }
    }
  }
}
</script>

<style lang="css" scoped>
.checkbox{
  padding: 0px;
  margin: 0px;
}
.legend-marker{
  height: 20px;
}
</style>
