<template lang="html">
  <div id="map">
    <div class="filter-container" v-show="!showLoaders">
      <MarkersFilters></MarkersFilters>
    </div>
  </div>
</template>

<script>
import MarkersFilters from '@/components/MarkersFilters'
import mapboxgl from 'mapbox-gl'
mapboxgl.accessToken = "pk.eyJ1IjoiY2FydG9uZyIsImEiOiJjazJldzVobGkwOWRxM2hzNTB1M3o2cG94In0.w7FyG31FWqXm3vXSh6WtxQ"
import {toGeoJSON, getBoundingBoxFromCoordinatesArray} from '@/helpers/mapUtils'
import {nigeriaBoundaries} from '@/constants/map/countryBoundaries'
import {layers} from '@/constants/map/layersDefinition'
const devEnv = process.env.NODE_ENV === 'development'
const imgURL = devEnv ? '/markers/' : `./markers/`

export default {
  name: "TheMap",
  components:{
    MarkersFilters
  },
  data(){
    return {
      imgURL: imgURL,
      markersImagesList: ['default', 'default_selected', 'spontaneous_site', 'spontaneous_site_selected'],
      imageLoadedCount: 0,
      nigeriaBoundaries: nigeriaBoundaries,
      countryBbox: null,
      stateBbox: null,
      lgaBbox: null
    }
  },
  mounted(){
    this.initMap()
  },
  computed:{
    showLoaders(){
      return this.$store.state.showLoaders
    },
    camps(){
      return {
        selectedCamp: toGeoJSON(this.$store.state.camps.filter(x => x.id == this.$store.state.selectedCamp)),
        otherCamps: toGeoJSON(this.$store.state.filteredCamps.filter(x => x.id != this.$store.state.selectedCamp))
      }
    },
    selectedCamp() {
      return this.$store.state.selectedCamp
    },
    statesBoundaries(){
      return this.$store.state.statesBoundaries
    },
    lgasBoundaries(){
      return this.$store.state.lgasBoundaries
    },
    filteredStates(){
      return this.$store.state.filteredStates
    },
    filteredLgas(){
      return this.$store.state.filteredLgas
    }
  },
  watch:{
    camps(){
      if (this.camps.otherCamps.features.length > 0) {
        this.map.getSource('otherCamps').setData(this.camps.otherCamps)
        this.map.getSource('selectedCamp').setData(this.camps.selectedCamp)
      }
    },
    // Load Source when all markers icons are loaded
    imageLoadedCount(){
      if (this.imageLoadedCount === this.markersImagesList.length) {
        this.addCampsSources()
      }
    },
    selectedCamp(){
      if (this.selectedCamp === null || this.selectedCamp === undefined) {
        this.setZoom()
      } else {
        this.map.flyTo({
          center: [this.camps.selectedCamp.features[0].geometry.coordinates[0], this.camps.selectedCamp.features[0].geometry.coordinates[1]],
          zoom: 11
        })
      }
    },
    filteredStates(){
      this.classifyBoundaries('state', this.filteredStates)
      this.setZoom()
    },
    filteredLgas(){
      this.classifyBoundaries('lga', this.filteredLgas)
      this.setZoom()
    }
  },
  methods:{
    initMap(){
      this.map = new mapboxgl.Map({
        container: 'map',
        style: 'mapbox://styles/mapbox/streets-v11',
        center: [7.5, 9],
        zoom: 4.7,
        minZoom: 4.7
      })

      this.map.on('load', async () => {
        this.markersImagesList.map(img => {
          this.map.loadImage(`${imgURL}${img}.png`, (error, image) => {
            this.imageLoadedCount ++
             this.map.addImage(img, image)
            if (error) throw error;
          });
        })
        if (this.countryBbox == null) {
          this.countryBbox = this.map.getBounds();
        }

        // Add grey mask around Nigeria
        this.map.addSource('nationalBoundaries', {
          'type': 'geojson',
          'data': this.nigeriaBoundaries
        })
        this.map.addLayer(layers.countryBoundaries);

        this.map.addSource('statesBoundaries', {
          'type': 'geojson',
          'data': this.statesBoundaries
        })
        this.map.addLayer(layers.statesBoundaries)

        this.map.addSource('lgasBoundaries', {
          'type': 'geojson',
          'data': this.lgasBoundaries
        })
        this.map.addLayer(layers.lgasBoundaries)
      })
    },
    addCampsSources(){
      this.map.addSource('otherCamps', {
        'type': 'geojson',
        'data': this.camps.otherCamps,
        cluster: true,
        clusterMaxZoom: 14, // Max zoom to cluster points on
        clusterRadius: 50
      });

      this.map.addSource('selectedCamp', {
        'type': 'geojson',
        'data': this.camps.selectedCamp
      });

      this.map.once('style.load', this.addCampsLayer())

    },
    addCampsLayer(){
      // ADD CLUSTERS
      this.map.addLayer(layers.clusters);
      this.map.addLayer(layers.clustersCount);

    // CLUSTERS BEHAVIOR
    this.map.on('click', 'clusters', (e) => {
      const features = this.map.queryRenderedFeatures(e.point, {
        layers: ['clusters']
      });
      const clusterId = features[0].properties.cluster_id;
      this.map.getSource('otherCamps').getClusterExpansionZoom(
        clusterId,
        (err, zoom) => {
          if (err) return;
          this.map.easeTo({
            center: features[0].geometry.coordinates,
            zoom: zoom
          });
        }
      );
    });

    // ADD CAMPS
    this.map.addLayer(layers.camps);

    // CAMPS BEHAVIOR
    this.map.on('click', 'camps', (e) => {
      this.$store.commit('setSelectedCamp', e.features[0].properties.id)
    })
    let popup = new mapboxgl.Popup({
      closeButton: false,
      closeOnClick: false
    });
    this.map.on('mouseenter', 'camps', (e) => {
      this.map.getCanvas().style.cursor = 'pointer';
      const coordinates = e.features[0].geometry.coordinates.slice()
      const description = `<h4 class="primary--text">${e.features[0].properties.name}</h4>`
      popup.setLngLat(coordinates).setHTML(description).addTo(this.map)
    });
    this.map.on('mouseleave', 'camps', () => {
      this.map.getCanvas().style.cursor = ''
      popup.remove()
    });
    this.map.on('mouseenter', 'camp', (e) => {
      this.map.getCanvas().style.cursor = 'pointer';
      const coordinates = e.features[0].geometry.coordinates.slice()
      const description = `<h4 class="primary--text">${e.features[0].properties.name}</h4>`
      popup.setLngLat(coordinates).setHTML(description).addTo(this.map)
    });
    this.map.on('mouseleave', 'camp', () => {
      this.map.getCanvas().style.cursor = ''
      popup.remove()
    });

    // ADD SELECTED CAMP
    this.map.addLayer(layers.selectedCamp)
    },
    setZoom(){
      this.map.fitBounds(this.lgaBbox || this.stateBbox || this.countryBbox)
    },
    classifyBoundaries(level, features){
      let bbox, boundaries, attribute
      if (level === 'lga') {
        bbox = 'lgaBbox'
        boundaries = 'lgasBoundaries'
        attribute = 'dscodeadm2'
      } else if (level === 'state') {
        bbox = 'stateBbox'
        boundaries = 'statesBoundaries'
        attribute = 'dscodeadm1'
      }
      if (features.length === 1) {
        for (let s of this[boundaries].features) {
          if (s.properties[attribute] === features[0].id) {
            s.properties.transparent = 'yes'
            this[bbox] = getBoundingBoxFromCoordinatesArray(s.geometry.coordinates)
          } else {
            s.properties.transparent = 'no'
          }
        }
      } else {
        this[boundaries].features.map(s => s.properties.transparent = 'yes')
        this[bbox] = null
      }
      this.map.getSource(boundaries).setData(this[boundaries])
    }
  }
}
</script>

<style lang="css" scoped>
  #map{
    width: 100%;
    height: 100%;
    border-radius: 15px;
  }
  .filter-container{
    position: absolute;
    bottom: 20px;
    left: 20px;
    z-index: 2500;
    background-color: white;
    padding: 10px;
    border-radius: 5px;
  }

</style>
