import Vue from 'vue'
import Vuex from 'vuex'
import * as Axios from 'axios'
import * as Papa from 'papaparse'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    showLoaders:true,
    datasetLoaded: false,
    dataError: false,
    raisedError:null,
    submissions:{},
    states:[],
    lgas:[],
    camps:[],
    statesBoundaries: {},
    lgasBoundaries:{},
    filteredStates:[],
    filteredLgas:[],
    filteredCamps:[],
    showFormalCamps:true,
    showInformalCamps:true,
    surveyConfig:{},
    labels:[],
    selectedCamp: null,
    showPDF: false
  },
  mutations: {
    setShowLoaders(state,value){
      state.showLoaders = value
    },
    // Raise Error
    setDataLoadingError(state, error){
      state.dataError = true
      state.raisedError = error
    },
    // Triggered by dropdown lists components
    filterStates(state, selected=null){
      selected === null ? state.filteredStates = state.states : state.filteredStates = state.states.filter(x => x.id === selected)
    },
    filterLgas(state, selected=null){
      if (selected === null) {
        state.filteredLgas = state.lgas.filter(x => state.filteredStates.some(s => s.id === x.state))
      } else {
        state.filteredLgas = state.lgas.filter(x => x.id === selected)
      }
    },
    filterCamps(state){
      let camps = state.camps
      if (!state.showFormalCamps) {
        camps = camps.filter(x => x.site_type !== "Formal")
      }
      if (!state.showInformalCamps) {
        camps = camps.filter(x => x.site_type !== "Informal")
      }
      state.filteredCamps = camps.filter(x => state.filteredLgas.some(s => s.id === x.lga))
    },
    //Triggered by Map or list component
    setSelectedCamp(state, camp){
      state.selectedCamp = camp
    },
    // Triggered by map component
    togglFormalCamps(state, v){
      state.showFormalCamps = v
    },
    togglInformalCamps(state, v){
      state.showInformalCamps = v
    },
    setShowPDF(state, v){
      state.showPDF = v
    }
  },
  actions: {
    async getData(context){
      try {
        // BUILD SURVEY CONFIGURATION
        const survey = {}
        const surveyConfig = await Axios.get('./data/survey_config_j.csv')
        Papa.parse(surveyConfig.data,{
          header: true,
          complete: results => {
            for (let r of results.data) {
              if (r.csv_field != undefined) {
                if (r.category.length === 0) {
                  survey[`${r.csv_field.toLowerCase()}`] = {...r}
                } else {
                  survey[`${r.category.toLowerCase()}/${r.csv_field.toLowerCase()}`] = {...r}
                }

              }
            }
            this.state.surveyConfig = survey
          },
          error: e => {
            console.log(e);
          }
        })


        // SET CHOICES LABELS
        const labels = await Axios.get('./data/labels.csv')
        Papa.parse(labels.data,{
          header: true,
          complete: results => {
            results.data.pop()
            this.state.labels = results.data
          },
          error: e => {
            console.log(e);
          }
        })


        // GET KOBO DATA
        const URL = "https://kobo.humanitarianresponse.info/api/v2/assets/aJ7uW4DUnpfsn6kRjvQWFq/data.json"
        const koboReqOptions = {
          method: 'get',
          url: URL,
          // params: { format: 'json' },
          headers: { Authorization: `Token c6d24e0275d1ca5be2231496a3ceeddd5e78bdc4` }
        }
        const koboRes = await Axios(koboReqOptions)
        let data = {}
        let states = []
        let lgas = []
        let camps = []
        for (let d of koboRes.data.results) {
          if (data[d['group_general_info/site']] ) {
            if (new Date(data[d['group_general_info/site']]._submission_time) < new Date(d._submission_time)) {
              data[d['group_general_info/site']] = d
            }
          } else {
            data[d['group_general_info/site']] = d
          }
        }
        this.state.submissions = data


        //CREATE LIST OF STATES / LGAS / CAMPS FROM KOBO DATA
        for (let d in data) {
          states.push({
            id: data[d]['group_general_info/state'],
            name: data[d]['group_general_info/state_name']
          })
          lgas.push({
            id: data[d]['group_general_info/lga'],
            name: data[d]['group_general_info/lga_name'],
            state: data[d]['group_general_info/state']
          })
          camps.push({
            id: data[d]['group_general_info/site'],
            name: data[d]['group_general_info/site_name'],
            searchName: `${data[d]['group_general_info/site']} - ${data[d]['group_general_info/site_name']}`,
            lat: data[d]['group_general_info/latitude'],
            long: data[d]['group_general_info/longitude'],
            lga: data[d]['group_general_info/lga'],
            site_type: data[d]['group_general_info/site_type']
          })
        }

        states = states.reduce((acc, x) =>
           acc.concat(acc.find(y => y.id === x.id) ? [] : [x])
         , []);
         this.state.states = states

        lgas = lgas.reduce((acc, x) =>
          acc.concat(acc.find(y => y.id === x.id) ? [] : [x])
        , []);
        this.state.lgas = lgas

        camps.sort((a, b) => a.name.localeCompare(b.name))
        this.state.camps = camps


        // GET GEOGRAPHIC BOUNDARIES
        const statesBoundaries = await fetch('https://gis.unhcr.org/arcgis/rest/services/core/wrl_adm1_polbnd_a_unhcr/FeatureServer/0/query?where=iso3+%3D+%27NGA%27&outFields=*&f=geojson')
        this.state.statesBoundaries = await statesBoundaries.json()

        let lgaWhereClause = ''
        states.map(x => lgaWhereClause += `dscodeadm1 = '${x.id}' OR `)
        lgaWhereClause = lgaWhereClause.substring(0, lgaWhereClause.length -3)
        const lgasBoundaries = await fetch(`https://gis.unhcr.org/arcgis/rest/services/core/wrl_adm2_polbnd_a_unhcr/FeatureServer/0/query?where=${lgaWhereClause}&outFields=*&f=geojson`)
        this.state.lgasBoundaries = await lgasBoundaries.json()

        context.commit("filterStates")
        context.commit("filterLgas")
        context.commit("filterCamps")

        this.state.datasetLoaded = true
        context.commit("setShowLoaders", false)

      } catch (e) {
        context.commit("setDataLoadingError", e)
      }
    }
  },
  modules: {
  }
})
