<template>
  <div class="age-pyramid-chart-container">
    <div
      class="chart-container"
    >
      <canvas
        ref="chartCanvas"
        :id="id"
      ></canvas>
    </div>
  </div>
</template>

<script>

import Chart from 'chart.js'
import chartConfig from '@/constants/charts/agePyramid'

export default {
  name: 'AgePyramidChart',
  props: {
    id: {
      type: String,
      required: true
    },
    surveyData:{
      type: Array,
      required: true
    }
  },
  data(){
    return {
      males: ['Males 0-5 years:','Males 6-17 years:','Males 18-35 years:','Males 36-59 years:','Males 60 or older:'],
      females: ['Females 0-5 years:','Females 6-17 year:','Females 18-35 years:','Females 36-59 years:','Females 60 or older:'],
      labels: ['60 or older','36-59 years','18-35 years','6-17 years','0-5 years']
    }
  },
  computed: {
    chartDatasets () {
      const maleValues = []
      const femaleValues = []
      this.males.map(x => {
        for (let d of this.surveyData) {
          if (d.label == x) maleValues.push(0 - parseInt(d.value))
        }
      })
      this.females.map(x => {
        for (let d of this.surveyData) {
          if (d.label == x) femaleValues.push(parseInt(d.value))
        }
      })
      return [
        { label: 'Female', backgroundColor: '#f37788', data: femaleValues.reverse() },
        { label: 'Male', backgroundColor: '#4095cd', data: maleValues.reverse()  }
      ]
    }
  },
  methods:{
    buildChart(){
      if (this.chart != undefined) {
        this.chart.destroy()
      }
      const labelsWithNumbers = []
      for (var i = 0; i < this.labels.length; i++) {
        labelsWithNumbers.push(`${this.labels[i]}: ${parseInt(this.chartDatasets[1].data[i])*-1 || 0} males - ${this.chartDatasets[0].data[i] || 0} females`)
      }
      const data = { datasets: this.chartDatasets, labels: labelsWithNumbers}
      const agePyramidChartConfig = { ...chartConfig, ...{ data } }
      const context = this.$refs.chartCanvas.getContext('2d')
      context.height = 500;
      this.chart = new Chart(context, agePyramidChartConfig)

      window.addEventListener('resize', () => {
        this.chart.destroy()
        this.chart = new Chart(context, agePyramidChartConfig)
      })
    }
  },

  mounted () {
    this.buildChart()
  },
  updated(){
    this.buildChart()
  }
}

</script>

<style scoped>
canvas {
  margin: auto;
}
.age-pyramid-chart-container {
  margin: 12px 5px 16px 0;
  border: 1px solid #b3b3b3;
  height: 200px;
}
.chart-container{
  height: 100%;
}
</style>
