export default {
  type: 'horizontalBar',
  data: {},
  options: {
    responsive: true,
    maintainAspectRatio: false,
    legend: {
      position: 'top',
      reverse: true
    },
    title: {
      display: true,
      fontSize: 14,
      text: 'Age Pyramid'
    },
    scales: {
      xAxes: [{
        ticks: {
          callback: (label) => label < 0 ? 0 - label : label
        }
      }],
      yAxes: [{
        stacked: true,
        barThickness: 15
      }]
    },
    tooltips: {
      callbacks: {
        obj: (t, d) => d.datasets[t[0].datasetIndex].label + ': ' + d.labels[t[0].index],
        label: (t, d) => t.datasetIndex === 1
          ? Number(0 - d.datasets[1].data[t.index])
          : d.datasets[0].data[t.index]
      }
    }
  }
}
