export const getTrafficLight = (tl, value) => {
  const green = '#62BB62'
  const yellow = '#ffeb0a'
  const orange = '#FFA90A'
  const red = '#DA5A56'
  const grey = '#9D9D9D'

  if (tl == 'yesno') {
    return value == 'yes' ? green : value == 'no' ? red : grey
  } else if (tl == 'noyes') {
    return value == 'yes' ? red : value == 'no' ? green : grey
  } else if (tl == 'dedicatedSfVsPopulation') {
    return value <= 2500 ? green : value > 2500 ? red : grey
  } else if (tl == 'mobileSfVsPopulation') {
    return value <= 500 ? green : value > 500 ? red : grey
  } else if (tl == 'PartnerPresenceLivelihood' || tl == 'PartnerPresenceEducation' || tl == 'PartnerPresenceFoodSecurity' || tl == 'PartnerPresenceHealth' || tl == 'PartnerPresenceNutrition' || tl == 'PartnerPresenceProtection' || tl == 'PartnerPresenceShelter') {
    return value.toLowerCase() == 'none' ? red : green
  } else if (tl == 'DistanceToX') {
    const dist = parseInt(value.substring(value.length - 1))
    if (dist < 5) {
      return green
    } else if (dist < 10) {
      return orange
    } else if (dist > 10) {
      return red
    } else {
      return grey
    }
  } else if (tl == 'TotalMalnutrition' || tl == 'NumberFemaleChildwithDisability' || tl == 'NumberMaleChildwithDisability' || tl == 'NumberFemaleAdultswithDisability' || tl == 'NumberMaleAdultswithDisability' || tl == 'NumberHHLivingOutside' || tl == 'NumberHHSharingShelter' || tl == 'NumberTransitionaldamagedShelters') {
    return parseInt(value) >= 1  ? red : green
  } else if (tl == 'nfidistrange') {
    if (value == 'within_1month') {
      return green
    } else if (value == 'within_2month') {
      return yellow
    } else if (value == 'within_3month') {
      return orange
    } else {
      return red
    }
  } else if (tl == "NumberLitresppPerday") {
    return parseInt(value) >= 15  ? green : red
  } else if (tl == "WaterWaitingTime") {
    if (value == 'time1') {
      return green
    } else if (value == 'time2') {
      return yellow
    } else if (value == 'time3') {
      return orange
    } else {
      return red
    }
  } else if (tl == 'TotalLatrinesVsIndidual' || tl == 'TotalShowersVsIndidual') {
    if (parseInt(value) <= 20) {
      return green
    } else if (parseInt(value) <= 25) {
      return yellow
    } else {
      return red
    }
  }
  else {
    return null
  }
}
