<template lang="html">
  <div class="">
    <div class="d-flex mb-7 flex-wrap">
      <div class="d-flex justify-space-around header">
        <div class="d-flex align-center justify-start">
          <img :src="imgUrl+'sector_logo.png'" alt="" class="appLogo">
        </div>
        <div class="d-flex align-center justify-end">
          <h3 class="secondary--text">  CCCM SITE TRACKER NIGERIA </h3>
        </div>
      </div>
      <div class="d-flex align-center justify-space-between filters-container mt-0 pt-0">
        <div class="d-flex filter pa-4">
          <v-select
            v-model="selectedState"
            :items="states"
            item-text="name"
            label="Select a State"
            item-value='id'
            clearable
            color='secondary'
          ></v-select>
        </div>
        <div class="d-flex filter pa-4">
          <v-select
            v-model="selectedLga"
            :items="lgas"
            item-text="name"
            label="Select a Local Government Area"
            item-value='id'
            clearable
            color='secondary'
          ></v-select>
        </div>
        <div class="d-flex filter pa-4">
          <v-autocomplete
            v-model="selectedCamp"
            :items="camps"
            item-text="searchName"
            label="Select a Camp"
            item-value='id'
            color='secondary'
            clearable
          ></v-autocomplete>
        </div>
      </div>
    </div>
    <div class="map-container mb-7">
      <the-map v-if="datasetLoaded"></the-map>
    </div>

  </div>
</template>

<script>
import TheMap from '@/components/Map'
const devEnv = process.env.NODE_ENV === 'development'
const imgURL = devEnv ? '/img/' : `./img/`

export default {
  name: 'Header',
  components:{
    TheMap
  },
  data(){
    return {
      imgUrl: imgURL,
      selectedState: null,
      selectedLga: null
    }
  },
  computed:{
    datasetLoaded(){
      return this.$store.state.datasetLoaded
    },
    states(){
      return this.$store.state.filteredStates
    },
    lgas(){
      return this.$store.state.filteredLgas
    },
    camps(){
      return this.$store.state.filteredCamps
    },
    selectedCamp:{
      get(){
        return this.$store.state.selectedCamp
      },
      set(c){
        this.$store.commit('setSelectedCamp', c)
      }
    }
  },
  watch:{
    selectedState(){
      if (this.selectedState === undefined) {
        this.$store.commit("filterStates")
      } else {
        this.$store.commit("filterStates", this.selectedState)
      }
      this.$store.commit("filterLgas")
      this.$store.commit("filterCamps")
    },
    selectedLga(){
      if (this.selectedLga === undefined) {
        this.$store.commit("filterLgas")
      } else {
        this.$store.commit("filterLgas", this.selectedLga)
      }
      this.$store.commit("filterCamps")
    }
  }
}
</script>

