<template lang="html">
  <div class="d-flex flex wrap flex-column">
    <div class="pdf-container pb-8" ref="pdfDiv1">
      <div class="pdf-header pr-5">
        <div class="pdf-header-sub">
          <img :src="imgURL+'img/sector_logo.png'" alt="" class="pdfLogo ml-2" id="pdfLogo">
        </div>
        <div class="pdf-header-sub">
          <h2 class="secondary--text mr-5">{{siteName}}</h2>
        </div>
      </div>
      <div class="d-flex flex-wrap pdf-content pb-8">
        <div class="category-container">
          <div class="d-flex category-container">
            <div class="pdf-map-container"></div>
            <div class="pdf-general-info" ref="infoBox">
              <div class="pdf-category-header pl-3">
                <h3>General Information</h3>
              </div>
              <v-simple-table dense>
                <template v-slot:default>
                  <tbody>
                    <tr
                      v-for="item in filteredDataset.general_info.data"
                      :key="`${item.question}+${item.answer}`"
                    >
                      <td>
                        <div class="d-flex align-center">
                          <span class="square mr-2" :style="{'background-color': item.tl}" v-if="item.tl != null"></span>
                          <span class="subtitle-2">{{ item.question }}</span>
                        </div>
                      </td>
                      <td><span class="subtitle-2">{{ item.answer }}</span></td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </div>
          </div>
        </div>
        <div v-for="cat in filteredDataset.other1" :key="cat.name" class="category-container2 mr-1 ml-1">
          <div class="d-flex flex-wrap category-container mt-8">
            <div class="pdf-category-header pl-3">
              <h3>{{cat.name}}</h3>
            </div>
              <div v-if="cat.name === 'Demographic Distribution'" class="d-flex flex-wrap category-container">
              <AgePyramidChart
                v-if="cat.data['age_pyramid#1']"
                :id="cat.data['age_pyramid#1'][0].value+Math.floor(Math.random() * Math.floor(100000))"
                :surveyData="cat.data['age_pyramid#1']"
                class="category-container"
              >
              </AgePyramidChart>
              <DoughnutChart
                v-if="cat.data['doughnut_chart#2']"
                :id="cat.data['doughnut_chart#2'][0].value+Math.floor(Math.random() * Math.floor(100000))"
                :surveyData="cat.data['doughnut_chart#2']"
                class="category-container"
              >
              </DoughnutChart>
            </div>
            <v-simple-table dense class="mt-1">
              <template v-slot:default>
                <tbody>
                  <tr
                    v-for="item in cat.data"
                    :key="`${item.question}+${item.answer}`"
                  >
                    <td>
                      <div class="d-flex align-center">
                        <span class="square mr-2" :style="{'background-color': item.tl}" v-if="item.tl != null"></span>
                        <span class="subtitle-2">{{ item.question }}</span>
                      </div>
                    </td>
                    <td><span class="subtitle-2">{{ item.answer }}</span></td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </div>
        </div>
      </div>
    </div>

    <div class="pdf-container pb-8" ref="pdfDiv2">
      <div class="d-flex flex-wrap pdf-content">
        <div v-for="cat in filteredDataset.other2" :key="cat.name" class="category-container2 mr-1 ml-1">
          <div class="d-flex flex-wrap category-container mt-8">
            <div class="pdf-category-header pl-3">
              <h3>{{cat.name}}</h3>
            </div>
            <PolarChart
              v-if="cat.name == 'Shelter - NFI' && cat.data['polar_area_chart#3']"
              :id="cat.data['polar_area_chart#3'][0].value+Math.floor(Math.random() * Math.floor(100000))"
              :surveyData="cat.data['polar_area_chart#3']"
              class="category-container"
            >
            </PolarChart>
            <v-simple-table dense>
              <template v-slot:default>
                <tbody>
                  <tr
                    v-for="item in cat.data"
                    :key="`${item.question}+${item.answer}`"
                  >
                    <td>
                      <div class="d-flex align-center">
                        <span class="square mr-2" :style="{'background-color': item.tl}" v-if="item.tl != null"></span>
                        <span class="subtitle-2">{{ item.question }}</span>
                      </div>
                    </td>
                    <td><span class="subtitle-2">{{ item.answer }}</span></td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </div>
        </div>
      </div>
    </div>
    <div id="pdfmap" v-bind:style="{height: mapHeight, width:mapWidth}" class="mt-4"></div>
    <div ref="pdfDiv5">
      <Footer
        :initialShowContact='false'
        initialShowAdvisory
      ></Footer>
    </div>

  </div>

</template>

<script>
import jsPDF from 'jspdf'
import html2canvas from 'html2canvas'
import mapboxgl from 'mapbox-gl'
mapboxgl.accessToken = "pk.eyJ1IjoiY2FydG9uZyIsImEiOiJjazJldzVobGkwOWRxM2hzNTB1M3o2cG94In0.w7FyG31FWqXm3vXSh6WtxQ"
import {toGeoJSON} from '@/helpers/mapUtils'
import AgePyramidChart from '@/components/AgePyramidChart'
import DoughnutChart from '@/components/DoughnutChart'
import PolarChart from '@/components/PolarChart'
import Footer from "@/components/Footer"
const devEnv = process.env.NODE_ENV === 'development'
const imgURL = devEnv ? '/' : `./`

export default {
  name:"ExportPDF",
  components:{
    AgePyramidChart,
    DoughnutChart,
    PolarChart,
    Footer
  },
  mounted(){
    this.showLoaders = true
    this.mapHeight = `${this.$refs.infoBox.clientHeight}px`
    this.mapWidth = `${this.$refs.pdfDiv1.clientWidth - this.$refs.infoBox.clientWidth}px`
    this.initMap()
  },
  data(){
    return {
      imgURL: imgURL,
      mapHeight: "7cm",
      mapWidth: '7cm',
      images:{},
      imagesLoadedCount: 0
    }
  },
  props: ['dataset'],
  computed:{
    filteredDataset(){
      let result = {
        other1:[],
        other2:[]
      }
      for (let k in this.dataset) {
        if (k === "general_info") {
          result.general_info = this.dataset[k]
        } else if (k === 'demographic_distribution' || k === 'cccm' || k === 'livelihood' || k === 'education' || k === 'food_security' || k === 'nutrition'){
          result.other1.push(this.dataset[k])
        } else {
          result.other2.push(this.dataset[k])
        }
      }
      return result
    },
    siteName(){
      const name = this.dataset.general_info.data.filter(x => x.field === 'site_name')
      return name[0].answer
    },
    showLoaders:{
      get(){
        return this.$store.state.showLoaders
      },
      set(v){
        this.$store.commit('setShowLoaders', v)
      }
    },
    showPDF:{
      get(){
        return this.$store.state.showPDF
      },
      set(v){
        this.$store.commit('setShowPDF', v)
      }
    },
    camp(){
      return toGeoJSON(this.$store.state.camps.filter(x => x.id == this.$store.state.selectedCamp))
    }
  },
  watch:{
    imagesLoadedCount(){
      if (this.imagesLoadedCount === 4) {
        this.ExportPDF()
      }
    }
  },
  methods:{
    initMap(){
      this.map = new mapboxgl.Map({
        container: 'pdfmap',
        style: 'mapbox://styles/mapbox/streets-v11',
        center: [this.camp.features[0].geometry.coordinates[0], this.camp.features[0].geometry.coordinates[1]],
        zoom: 7,
        preserveDrawingBuffer: true
      })

      this.map.on('load', async () => {
        this.map.resize()
        this.map.loadImage(`${this.imgURL}markers/default.png`, (error, image) => {
           this.map.addImage("default", image)
          if (error) throw error;
        });
        this.map.addSource('camp', {
          'type': 'geojson',
          'data': this.camp
        });
        this.map.addLayer({
          'id': 'camp',
          'type': 'symbol',
          'source': 'camp',
          'layout': {
            'icon-image': 'default',
            'icon-size': 0.025
          }
        })
        this.map.once('style.load', this.generateImage())
      })
    },
    async generateImage(){

      const part1 = new Image()
      const part1src = await html2canvas(this.$refs.pdfDiv1, {
        scrollX: 0,
        scrollY: -window.scrollY
      })
      part1.src = await part1src.toDataURL('image/jpeg', 0.7)
      part1.onload = () => {
        this.images.part1 = part1
        this.imagesLoadedCount ++
      }
      const part2 = new Image()
      const part2src = await html2canvas(this.$refs.pdfDiv2, {
        scrollX: 0,
        scrollY: -window.scrollY
      })
      part2.src = await part2src.toDataURL('image/jpeg', 0.7)
      part2.onload = () => {
        this.images.part2 = part2
        this.imagesLoadedCount ++
      }
      const footerImg = new Image()
      const footerSrc = await html2canvas(this.$refs.pdfDiv5, {
        scrollX: 0,
        scrollY: -window.scrollY
      })
      footerImg.src = await footerSrc.toDataURL('image/jpeg', 0.7)
      footerImg.onload = () => {
        this.images.footerImg = footerImg
        this.imagesLoadedCount ++
      }
      const mapImg = new Image()
      mapImg.src = await this.map.getCanvas().toDataURL('image/jpeg', 0.7)
      mapImg.onload = () => {
        const ratio = this.images.part1.width / 210
        mapImg.width = mapImg.width / ratio
        mapImg.height = mapImg.height / ratio
        this.images.mapImg = mapImg
        this.imagesLoadedCount ++
      }
    },
    ExportPDF(){
      const ratio = this.images.part1.width / 210
      const footerRatio = 210 / this.images.footerImg.width

      const doc = new jsPDF();
      doc.addImage(this.images.part1, "JPEG", 0, 0, this.images.part1.width/ratio, this.images.part1.height/ratio);
      doc.addImage(this.images.mapImg, "PNG",1,10, this.images.mapImg.width, this.images.mapImg.height)
      doc.addPage();
      doc.addImage(this.images.part2, "PNG", 0, 0, this.images.part2.width/ratio, this.images.part2.height/ratio);
      doc.addImage(this.images.footerImg, "PNG", 0, 295-(this.images.footerImg.height*footerRatio), this.images.footerImg.width*footerRatio, this.images.footerImg.height*footerRatio);
      this.showPDF = false
      this.showLoaders = false
      doc.save();
    }
  }
}
</script>

