export const toGeoJSON = (array) => {
  const geojson = {
    'type': 'FeatureCollection',
    'features': []
  }
  array.map(p => geojson.features.push({
    'type': 'Feature',
    'geometry': {
      'type': 'Point',
      'coordinates': [p.long, p.lat]
    },
    'properties': {
      id: p.id,
      name: p.name,
      site_type: p.site_type
    }
    })
  )
  return geojson
}

export const getBoundingBoxFromCoordinatesArray = (coordinates) => {
  let bounds = {}, latitude, longitude

  if (coordinates.length === 1) {
    // It's only a single Polygon
    for (let c of coordinates[0]) {
      longitude = c[0];
      latitude  = c[1];

      bounds.xMin = bounds.xMin < longitude ? bounds.xMin : longitude;
      bounds.xMax = bounds.xMax > longitude ? bounds.xMax : longitude;
      bounds.yMin = bounds.yMin < latitude ? bounds.yMin : latitude;
      bounds.yMax = bounds.yMax > latitude ? bounds.yMax : latitude;
    }

  } else {
    // It's a MultiPolygon
    // Loop through each coordinate set
    for (let coord of coordinates) {
      for (let c of coord) {
        longitude = c[0];
        latitude  = c[1];

        // Update the bounds recursively by comparing the current xMin/xMax and yMin/yMax with the current coordinate
        bounds.xMin = bounds.xMin < longitude ? bounds.xMin : longitude;
        bounds.xMax = bounds.xMax > longitude ? bounds.xMax : longitude;
        bounds.yMin = bounds.yMin < latitude ? bounds.yMin : latitude;
        bounds.yMax = bounds.yMax > latitude ? bounds.yMax : latitude;
      }
    }
  }

  // Returns an object that contains the bounds of this GeoJSON data.
  // The keys describe a box formed by the northwest (xMin, yMin) and southeast (xMax, yMax) coordinates.
  return [[bounds.xMin , bounds.yMin], [bounds.xMax, bounds.yMax]];
}
