<template>
  <v-app>
    <v-dialog
      v-model="showLoaders"
      persistent
      fullscreen
      content-class="loading-dialog"
    >
      <v-container fill-height>
        <v-layout row justify-center align-center>
          <clip-loader :color="loadingColor" v-if="!dataError"></clip-loader>
          <h2 v-if="dataError" class="primary--text">
            An error occured, please try again later. If the problem persists,
            please contact the application administrator.
          </h2>
        </v-layout>
      </v-container>
    </v-dialog>

    <v-container>
      <Header></Header>
      <survey-viewer v-if="selectedCamp != null"></survey-viewer>
      <Footer initialShowContact :initialShowAdvisory="false"></Footer>
    </v-container>
  </v-app>
</template>

<script>
import ClipLoader from "vue-spinner/src/ClipLoader.vue";
import Header from "./components/Header";
import Footer from "./components/Footer";
import SurveyViewer from "./components/SurveyViewer";

export default {
  name: "App",
  components: {
    ClipLoader,
    Header,
    SurveyViewer,
    Footer,
  },
  data: () => ({
    loadingColor: "#286090",
  }),
  computed: {
    showLoaders() {
      return this.$store.state.showLoaders;
    },
    dataError() {
      return this.$store.state.dataError;
    },
    selectedCamp() {
      return this.$store.state.selectedCamp;
    },
  },
  mounted() {
    this.$store.dispatch("getData");
  },
};
</script>

<style>
.loading-dialog {
  position: absolute;
  background-color: #ffffff;
  z-index: 3000 !important;
}
</style>
